@import url(https://fonts.googleapis.com/css?family=Dosis|Lobster|Playfair+Display|Rock+Salt|Quicksand|Playfair+Display);
.full {
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  font-size: 270%;
  font-family: "Lobster", cursive;
  text-align: center;
  height: 100vh;
}
.full a:not(.welcome):not(.home) {
  line-height: 30vh;
}
.full > a,
.iconsnav:not(.welcome) {
  border-left: 4px black solid;
  border-top: 4px black solid;
  color: gold;
  text-shadow: 2px 2px 2px grey;
}
.full a:not(.welcome):hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.full i:hover {
  color: black;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.responsive {
  display: none;
}
.home {
  grid-area: 1 / 1 / 4 / 1;
  line-height: 60vh;
}
.rose {
  grid-area: 1 / 4 / 5 / 5;
}
.rose img {
  width: 80vh;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  text-align: right;
  border-top: 4px black solid;
  background-image: url(/static/media/final824-roses-drawing.9c0c16ad.svg);
  background-size: cover;
  background-position: left;
  display: grid;
  place-items: center;
  border-left: none !important;
}
nav p {
  border-bottom: 4px black solid;
  width: 45%;
  font-size: 1.5em;
  text-align: center;
}
.iconsnav {
  border: none;
  font-size: 1em;
  grid-area: 4 / 1 / 4 / 4;
  display: inline;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.iconsnav i {
  color: gold;
}

.about {
  font-family: "Lobster", cursive;
  padding: 0 50px 50px 50px;
}
.about > p {
  font-family: "Quicksand", sans-serif;
  border-top: 1px solid gold;
  border-left: 1px solid gold;
  margin: 0 15px 0 15px;
  padding: 15px;
  text-align: justify;
}
.about > p:nth-of-type(-n+2){
  margin-bottom: 30px; 
}
.date p {
  /* background-color: gold; */
  width: 80px;
  height: 80px;
  line-height: 80px;
  border: 4px gold solid;
  border-radius: 50%;
  font-size: 3em;
}
.date {
  padding: 15px 30px 15px 20px;
  float: right;
  shape-outside: circle(50%);
}

.projects {
  font-family: "Lobster", cursive;
  padding: 0 50px 50px 50px;
}
.hprojects {
  margin-bottom: 15px;
}
h2 {
  font-size: 166%;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  color: gold;
}
h3 {
  font-family: "Quicksand", sans-serif;
}
.imagecenter {
  padding: 15px;
  text-align: center;
}
.projects img {
  max-width: 100%;
  max-height: 100%;
}
.projects ul {
  margin: 15px;
  padding: 15px 15px 0;
  border-top: 1px solid gold;
  border-left: 1px solid gold;
}
.projects ul:nth-of-type(2) {
  margin-bottom: 0;
}
.projects li {
  padding-bottom: 5px;
  font-family: "Quicksand", sans-serif;
}
.projects li:nth-child(6) {
  font-size: 1.5em;
  padding-top: 15px;
}
.projects i {
  padding: 15px;
}
.projects li a:hover {
  padding-bottom: 18px;
  border-bottom: 1px solid gold;
}

.contact {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  font-family: "Quicksand", sans-serif;
  padding: 50px;
}
form {
  margin-left: 15px;
}
input,
textarea {
  width: 40vw;
  border: 2px solid transparent;
  margin: 5px;
  padding: 5px;
  background-color: transparent;
  color: gold;
  font-size: 1.3em;
  font-family: "Quicksand", sans-serif;
}
textarea {
  height: 20vw;
}
.contact ul li:nth-child(1n + 1) {
  border-bottom: 1px solid gold;
}
.avatar {
  align-self: center;
  margin: 0 auto;
  background-image: url(/static/media/myAvatar.97a8dc08.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  height: 21vw;
  width: 21vw;
  border-radius: 50%;
  border: 3px solid white;
  text-align: center;
  margin-bottom: 25px;
}
h4 {
  display: inline-block;
  color: gold;
  font-size: 2em;
  position: relative;
  top: 101%;
  text-shadow: 2px 2px 2px black;
  font-family: "Lobster", cursive;
  line-height: 50px;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-moz-placeholder,
textarea::-moz-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-ms-input-placeholder,
textarea::-ms-input-placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::placeholder,
textarea::placeholder,
input[value="send"] {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input[value="send"]:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  /* font-family: "Rock Salt", cursive; */
  /* font-size: 0.7em; */
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  margin: 20px auto;
  max-width: 95vw;
  width: 100%;
  background: url(/static/media/background.d891b24c.jpg) no-repeat center center fixed;
}
code {
}
.projects,
.about,
.contact {
  margin: 15px auto;
  border-top: 4px solid gold;
  background-color: rgba(0, 0, 0, 0.4);
}
h1 {
  font-size: 60px;
  color: black;
  text-shadow: 2px 2px 2px grey;
  margin: 15px 15px 30px 15px;
}
h3,
p,
a,
i,
span {
  color: white;
}
.up {
  position: fixed;
  right: 3%;
  bottom: 2vh;
  z-index: 1;
}
.up i {
  font-size: 2em;
  color: gold;
}
.after {
  display: none;
}
footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}
footer i {
  color: black;
  font-size: 2em;
}
footer a {
  padding: 0 3% 0 3%;
}
footer i:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}
/* footer end */

.App-header {
  font-size: calc(5px + 2vmin);
}
@media screen and (max-width: 768px) {
  body {
    -ms-scroll-chaining: chained;
        overscroll-behavior: auto;
  }
  .full {
    display: none;
  }
  .responsive {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15px;
    font-size: 3em;
    text-align: center;
  }
  .responsive li {
    margin: 30px auto;
  }
  .responsive li a {
    border: 1px solid gold;
    border-radius: 50%;
    padding: 30px;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.6);
  }
  main {
    border: none;
  }
  .up {
    display: none;
  }
  .after {
    display: inline;
  }
  .after i {
    font-size: 2em;
    color: gold;
  }
  .about,
  .projects {
    padding: 0 0 30px 0;
  }
  h1 {
    font-size: 3em;
  }
  .projects li:nth-child(6) {
    font-size: 2em;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 30px;
  }
  .projects li a {
    padding: 15px;
    border: 2px solid white;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    display: grid;
    place-content: center;
  }
  ul span {
    display: none;
  }
  .contact form {
    display: none;
  }
  .avatar {
    width: 61vw;
    height: 61vw;
  }
  footer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.slide-in-elliptic-top-fwd {
  -webkit-animation: slide-in-elliptic-top-fwd 0.7s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-elliptic-top-fwd 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
}
@-webkit-keyframes slide-in-elliptic-top-fwd {
  0% {
    -webkit-transform: translateY(-600px) rotateX(-30deg) scale(0);
    transform: translateY(-600px) rotateX(-30deg) scale(0);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0) rotateX(0) scale(1);
    transform: translateY(0) rotateX(0) scale(1);
    -webkit-transform-origin: 50% 1400px;
    transform-origin: 50% 1400px;
    opacity: 1;
  }
}
@keyframes slide-in-elliptic-top-fwd {
  0% {
    -webkit-transform: translateY(-600px) rotateX(-30deg) scale(0);
    transform: translateY(-600px) rotateX(-30deg) scale(0);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0) rotateX(0) scale(1);
    transform: translateY(0) rotateX(0) scale(1);
    -webkit-transform-origin: 50% 1400px;
    transform-origin: 50% 1400px;
    opacity: 1;
  }
}

